var TableauController = function TableauController(applicationUrl, language) {
  function init() {
    _biz_bizagiTableau.setApplicationUrl(applicationUrl);
    _assignLanguage();
  }

  function _assignLanguage() {
    bizagi.resourceDefinitionLocation = {
      "es": bizagi.loader.getResource("l10n", "bizagi.tableau.resources.es"),
      "en": bizagi.loader.getResource("l10n", "bizagi.tableau.resources.en"),
      "default": bizagi.loader.getResource("l10n", "bizagi.tableau.resources.en") };

    bizagi.localization = new bizagi.l10n(bizagi.resourceDefinitionLocation);
    bizagi.localization.enableCustomizations = false;
    bizagi.localization.setLanguage(language);

    $.when(bizagi.localization.readyDeferred).done(function () {
      bizagi.templateService = new bizagi.templates.services.service(bizagi.localization);
      _biz_uiTableau.renderTmpl("bizagi.connector.tableau.login", function () {
        _biz_bizagiTableau.buildConnector();
        _deferredInitUi.resolve();
      });
    });
  }

  return {
    init: init };

};

var UITableau = function UITableau() {
  function loadTreeView(nextFn, fromBack) {
    $("#nextButton").click(nextFn);
    _importButton();
    var tableauTreeEl = document.getElementById("tableau-entity-tree");

    if (_biz_globalsTableau.entities.length === 0) {
      disableBtn("importButton");
      disableBtn("nextButton");
      document.getElementById("emptyEntityList").style.display = "block";
      return;
    }

    for (var i = 0; i < _biz_globalsTableau.entities.length; i++) {
      var entityNodeEl = document.createElement("li"),
      entity = _biz_globalsTableau.entities[i],
      divCheckboxEl = _createTreeCheckbox(entity.name, entity.name, true, false, i);

      entityNodeEl.className = "parent-node";
      entityNodeEl.appendChild(_createExpandButton("btn-" + entity.name));
      entityNodeEl.appendChild(divCheckboxEl);

      if (entity.attribs && entity.attribs.length > 0) {
        var childNodesEl = document.createElement("ul"),
        containerDivEl = document.createElement("div");
        containerDivEl.id = "container-nodes-" + entity.name;
        containerDivEl.className = 'tableau-collapsed';
        _loadChildNodes(entity, containerDivEl);
        childNodesEl.appendChild(containerDivEl);
        entityNodeEl.appendChild(childNodesEl);
      }

      tableauTreeEl.appendChild(entityNodeEl);
    }
    if (fromBack) {
      _loadConfiguration();
    }
  }

  function showFailedConnectionMessage() {
    $("#form-client-id").addClass("bz-form-item--error");
    $("#form-client-secret").addClass("bz-form-item--error");

    document.getElementById("authButton").disabled = false;
  }

  function disableBtn(btnId) {
    document.getElementById(btnId).disabled = true;
  }

  function renderTmpl(template, callback) {
    bizagi.templateService.getTemplate(bizagi.getTemplate(template)).
    done(function (resolvedTemplate) {
      $("#tableau-content").empty();
      $.tmpl(resolvedTemplate, {
        whiteLogo: (BIZAGI_ENVIRONMENT === 'release' ? 'jquery/production/' + bizagi.loader.productBuild + '/desktop/css/' : 'jquery/tableau/css/') + "images/logotype-bizagi-white.png",
        blackLogo: (BIZAGI_ENVIRONMENT === 'release' ? 'jquery/production/' + bizagi.loader.productBuild + '/desktop/css/' : 'jquery/tableau/css/') + "images/logotype-bizagi-black.png" }).
      appendTo("#tableau-content");
      if (callback !== undefined && typeof callback === 'function') callback();
    });
  }

  function loadEntityProcessTable(nextFn, backFn) {
    $("#nextButton").click(nextFn);
    $("#backButton").click(backFn);
    var tbodyEl = document.getElementById("tableau-entity-process-body"),
    entityIndexes = Object.keys(_biz_globalsTableau.processByEntity);

    for (var i = 0; i < entityIndexes.length; i++) {
      var entity = _biz_globalsTableau.entities[entityIndexes[i]];

      var rowEl = document.createElement("tr"),
      firstColumnEl = document.createElement("td"),
      secondColumnEl = document.createElement("td");

      firstColumnEl.className = "bz-table__cell";
      firstColumnEl.innerText = entity.name;
      secondColumnEl.appendChild(_createSelectEl(entityIndexes[i]));

      rowEl.appendChild(firstColumnEl);
      rowEl.appendChild(secondColumnEl);

      tbodyEl.appendChild(rowEl);
    }
  }

  // nextFn is only used for backButton's callback
  function loadResumeConfiguration(submitFn, nextFn) {
    $("#submitButton").click(submitFn);
    $("#backButton").click(function () {
      var backToTree = renderTmpl.bind(this, "bizagi.connector.tableau.tree", loadTreeView.bind(this, nextFn, true));
      if (Object.keys(_biz_globalsTableau.processByEntity).length === 0) {
        backToTree();
      } else {
        var resumeStep = renderTmpl.bind(
        this,
        "bizagi.connector.tableau.export",
        loadResumeConfiguration.bind(this, submitFn, nextFn));

        renderTmpl("bizagi.connector.tableau.processTable", loadEntityProcessTable.bind(this, resumeStep, backToTree));
      }
    });
    var tmpConfiguration = {
      entities: _biz_globalsTableau.selectedEntities,
      processes: _biz_globalsTableau.processByEntity };


    $("#selectConfiguration").click(function () {
      var range = document.createRange();
      range.selectNode(document.getElementById("tableau-container-configuration"));
      window.getSelection().removeAllRanges();
      window.getSelection().addRange(range);
    });
    document.getElementById("tableau-container-configuration").innerText = JSON.stringify(tmpConfiguration, null, 2);
  }

  function _createSelectEl(entityIndex) {
    var selectEl = document.createElement("select"),
    entity = _biz_globalsTableau.entities[entityIndex],
    processId = _biz_globalsTableau.processByEntity[entityIndex];

    selectEl.className = "bz-select";
    selectEl.onchange = function () {
      _biz_globalsTableau.processByEntity[entityIndex] = this.value;
    };
    for (var i = 0; i < entity.processesRelated.length; i++) {
      var optionEl = document.createElement("option");
      optionEl.className = "bz-option";
      optionEl.innerText = entity.processesRelated[i].displayName;
      optionEl.value = entity.processesRelated[i].id;
      selectEl.appendChild(optionEl);
    }
    selectEl.value = processId;
    return selectEl;
  }

  function _loadChildNodes(entity, childNodesEl) {
    for (var i = 0; i < entity.attribs.length; i++) {
      var attrib = entity.attribs[i],
      checkboxId = entity.name + '.' + attrib.name,
      leaf = attrib.xpaths.length === 1 || attrib.id === -1,
      nodeEl,divCheckboxEl;

      if (entity.isProcessEntity && attrib.id === -1) {
        for (var j = 0; j < attrib.xpaths.length; j++) {
          var attribName = attrib.xpaths[j].value,
          checkboxChildId = entity.name + "." + attribName;

          if (attribName.toLowerCase() === '@workitems') continue;

          nodeEl = document.createElement("li");
          divCheckboxEl = _createTreeCheckbox(checkboxChildId, attribName, false, leaf);
          nodeEl.appendChild(divCheckboxEl);
          childNodesEl.appendChild(nodeEl);
        }
        continue;
      } else if (attrib.id === -1) continue;

      divCheckboxEl = _createTreeCheckbox(checkboxId, attrib.name, false, leaf);
      nodeEl = document.createElement("li");
      if (!leaf)
      nodeEl.appendChild(_createExpandButton("btn-" + checkboxId));
      nodeEl.appendChild(divCheckboxEl);
      if (!leaf) {
        var childNodesRelEl = document.createElement("ul"),
        containerDivEl = document.createElement("div");
        nodeEl.className = 'parent-node';
        containerDivEl.id = "container-nodes-" + checkboxId;
        containerDivEl.className = 'tableau-collapsed';
        for (var j = 1; j < attrib.xpaths.length; j++) {
          var attribName = attrib.xpaths[j].value.split(".")[1],
          nodeRelEl = document.createElement("li"),
          checkboxChildId = checkboxId + '.' + attribName;
          nodeRelEl.appendChild(_createTreeCheckbox(checkboxChildId, attribName, false, true));
          containerDivEl.appendChild(nodeRelEl);
          childNodesRelEl.appendChild(containerDivEl);
        }
        nodeEl.appendChild(childNodesRelEl);
      }
      childNodesEl.appendChild(nodeEl);
    }
  }

  function _createTreeCheckbox(id, labelText, root, leaf, index) {
    if (!root) root = false;
    if (!leaf) leaf = false;
    var divCheckboxEl = document.createElement("div"),
    inputCheckboxEl = document.createElement("input"),
    labelCheckboxEl = document.createElement("label");
    divCheckboxEl.id = id;
    divCheckboxEl.className = "bz-checkbox";
    divCheckboxEl.setAttribute("bz-root", root);
    divCheckboxEl.setAttribute("bz-leaf", leaf);
    if (root) divCheckboxEl.setAttribute("bz-index", index);
    inputCheckboxEl.className = "bz-checkbox__input";
    labelCheckboxEl.className = "bz-checkbox__label";
    inputCheckboxEl.type = "checkbox";
    labelCheckboxEl.innerText = labelText;

    divCheckboxEl.onclick = function () {
      var inputCheckEL = this.getElementsByClassName("bz-checkbox__input")[0],
      childrenContainerEl = this.parentElement.getElementsByTagName("ul"),
      childNodesEl = [],
      rootElementId = this.id.split(".")[0],
      rootIndex = document.getElementById(rootElementId).getAttribute("bz-index"),
      parentElementId = this.id.substring(0, this.id.length - this.innerText.length - 1);

      if (childrenContainerEl.length > 0)
      childNodesEl = childrenContainerEl[0].getElementsByClassName("bz-checkbox__input");

      if (inputCheckEL.getAttribute("checked") === null) {
        var splittedId = this.id.split("."),
        elementId = "";
        for (var i = 0; i < splittedId.length; i++) {
          elementId += splittedId[i];
          var element = document.getElementById(elementId),
          checkboxEl = element.getElementsByClassName("bz-checkbox__input")[0];
          checkboxEl.setAttribute("checked", "checked");
          if (element.getAttribute("bz-root") != "true") {
            if (!_biz_globalsTableau.selectedEntities[rootIndex])
            _biz_globalsTableau.selectedEntities[rootIndex] = {};
            _biz_globalsTableau.selectedEntities[rootIndex][element.id] = 1;
          }
          elementId += ".";
        }

        for (var i = 0; i < childNodesEl.length; i++) {
          var element = childNodesEl[i].parentElement;
          childNodesEl[i].setAttribute("checked", "checked");
          if (element.getAttribute("bz-root") != "true") {
            if (!_biz_globalsTableau.selectedEntities[rootIndex])
            _biz_globalsTableau.selectedEntities[rootIndex] = {};
            _biz_globalsTableau.selectedEntities[rootIndex][element.id] = 1;
          }
        }
      } else {
        inputCheckEL.removeAttribute("checked");
        delete _biz_globalsTableau.selectedEntities[rootIndex][this.id];
        for (var i = 0; i < childNodesEl.length; i++) {
          var elementId = childNodesEl[i].parentElement.id;
          childNodesEl[i].removeAttribute("checked");
          delete _biz_globalsTableau.selectedEntities[rootIndex][elementId];
        }
        if (this.getAttribute("bz-root") == "true")
        delete _biz_globalsTableau.selectedEntities[rootIndex];else

        _updateParentElements(rootElementId, parentElementId, rootIndex);
      }
    };

    divCheckboxEl.appendChild(inputCheckboxEl);
    divCheckboxEl.appendChild(labelCheckboxEl);

    return divCheckboxEl;
  }

  function _updateParentElements(rootElementId, parentElementId, rootIndex) {
    var parentEl = document.getElementById(parentElementId),
    childrenContainerEl = parentEl.parentElement.getElementsByTagName("ul"),
    childNodesEl = [];

    if (childrenContainerEl.length > 0)
    childNodesEl = childrenContainerEl[0].getElementsByClassName("bz-checkbox__input");

    var uncheckParent = false;
    for (var i = 0; i < childNodesEl.length; i++) {
      if (childNodesEl[i].getAttribute("checked") != null) {
        uncheckParent = false;
        break;
      }
      uncheckParent = true;
    }
    if (uncheckParent) {
      parentEl.getElementsByClassName("bz-checkbox__input")[0].removeAttribute("checked");
      if (parentEl.getAttribute("bz-root") == "true")
      delete _biz_globalsTableau.selectedEntities[rootIndex];else

      delete _biz_globalsTableau.selectedEntities[rootIndex][parentEl.id];
    }
    if (rootElementId !== parentElementId && Object.keys(_biz_globalsTableau.selectedEntities[rootIndex]).length === 0) {
      var rootEl = document.getElementById(rootElementId);
      rootEl.getElementsByClassName("bz-checkbox__input")[0].removeAttribute("checked");
      delete _biz_globalsTableau.selectedEntities[rootIndex];
    }
  }

  function _createExpandButton(id) {
    var buttonEl = document.createElement("button");
    buttonEl.className = "bz-btn bz-btn--s tableau-node-expand";
    buttonEl.innerText = "+";
    buttonEl.id = id;
    buttonEl.onclick = function () {
      var entityName = this.id.substring(4, this.id.length),
      containerDivEl = document.getElementById("container-nodes-" + entityName);
      if (containerDivEl) {
        if (containerDivEl.className === "tableau-collapsed") {
          containerDivEl.className = "";
          this.innerText = "-";
        } else {
          containerDivEl.className = "tableau-collapsed";
          this.innerText = "+";
        }
      }
    };
    return buttonEl;
  }

  function _importButton() {
    $("#tableau-uploadConfiguration").change(function () {
      var file = this.files[0];
      if (file) {
        var fileReader = new FileReader();
        fileReader.onload = function (evt) {
          var content = evt.target.result;
          if (content !== '{}') {
            try {
              var contentJson = JSON.parse(content);
              _validateStructConfiguration(contentJson);
              document.getElementById("tableau-alert-error").style.display = "none";
              _loadConfiguration(contentJson);
            } catch (exception) {
              _showMessageAlert(bizagi.localization.dictionary["tableau-error-invalid-file"]);
            }
          }
        };
        fileReader.readAsText(file);
      }
    });

    $("#importButton").click(function () {
      var uploadFileEl = document.getElementById("tableau-uploadConfiguration");
      uploadFileEl.click();
    });
  }

  function _showMessageAlert(message) {
    var alert = document.getElementById("tableau-alert-error");
    alert.style.removeProperty("display");
    alert.getElementsByClassName("tableau-alert-message")[0].innerText = message;
  }

  function _validateStructConfiguration(configuration) {
    var keys = Object.keys(configuration);
    if (keys.length !== 2) {
      throw "invalid configuration";
    }
    var areValidProperties = keys[0] === "entities" && keys[1] === "processes";
    if (!areValidProperties) {
      throw "invalid configuration";
    }
  }

  function _loadConfiguration(configuration) {
    if (!configuration) {
      configuration = {
        entities: _biz_globalsTableau.selectedEntities,
        processes: _biz_globalsTableau.processByEntity };

    }

    var entityIndexes = Object.keys(_biz_globalsTableau.selectedEntities);
    for (var i = 0; i < entityIndexes.length; i++) {
      var attribs = Object.keys(_biz_globalsTableau.selectedEntities[entityIndexes[i]]),
      rootId = attribs[0].split(".")[0],
      rootCheckboxEl = document.getElementById(rootId);
      rootCheckboxEl.getElementsByClassName("bz-checkbox__input")[0].removeAttribute("checked");
      for (var j = 0; j < attribs.length; j++) {
        var element = document.getElementById(attribs[j]);
        element.getElementsByClassName("bz-checkbox__input")[0].removeAttribute("checked");
      }
    }
    _biz_globalsTableau.selectedEntities = {};
    _biz_globalsTableau.processByEntity = {};
    var oldEntityIndexes = Object.keys(configuration.entities);
    for (var i = 0; i < oldEntityIndexes.length; i++) {
      var xpaths = Object.keys(configuration.entities[oldEntityIndexes[i]]),
      rootId = xpaths[0].split(".")[0],
      rootCheckboxEl = document.getElementById(rootId);
      if (rootCheckboxEl) {
        var rootIndex = rootCheckboxEl.getAttribute("bz-index");
        rootCheckboxEl.getElementsByClassName("bz-checkbox__input")[0].setAttribute("checked", "checked");
        if (configuration.processes[oldEntityIndexes[i]]) {
          _biz_globalsTableau.processByEntity[rootIndex] = configuration.processes[oldEntityIndexes[i]];
        }
        for (var j = 0; j < xpaths.length; j++) {
          var checkboxEl = document.getElementById(xpaths[j]);
          if (checkboxEl) {
            checkboxEl.getElementsByClassName("bz-checkbox__input")[0].setAttribute("checked", "checked");

            // add to globals
            if (!_biz_globalsTableau.selectedEntities[rootIndex])
            _biz_globalsTableau.selectedEntities[rootIndex] = {};
            _biz_globalsTableau.selectedEntities[rootIndex][checkboxEl.id] = 1;
          }

        }
      }
    }
  }

  return {
    loadTreeView: loadTreeView,
    loadEntityProcessTable: loadEntityProcessTable,
    loadResumeConfiguration: loadResumeConfiguration,
    showFailedConnectionMessage: showFailedConnectionMessage,
    disableBtn: disableBtn,
    renderTmpl: renderTmpl };

};

var BizagiTableau = function BizagiTableau() {
  var _applicationUrl = null,
  _utilsTableau = new UtilsTableau();

  function setApplicationUrl(applicationUrl) {
    _applicationUrl = applicationUrl;
  }

  function getApplicationUrl() {
    return _applicationUrl;
  }

  function buildConnector() {
    if (!_utilsTableau.isNewWebEngine()) {
      // Fix related to: https://github.com/tableau/webdataconnector/issues/165
      if (!!window.tableauVersionBootstrap) {
        window._tableau.triggerInitialization();
      }
      if (!window.tableauVersionBootstrap) {
        var DOMContentLoaded_event = window.document.createEvent("Event");
        DOMContentLoaded_event.initEvent("DOMContentLoaded", true, true);
        window.document.dispatchEvent(DOMContentLoaded_event);
      }
    }

    $("#authButton").click(function () {
      connect($("#clientId").val(), $("#clientSecret").val());
    });
  }

  function connect(clientId, clientSecret) {
    _biz_uiTableau.disableBtn('authButton');
    _utilsTableau.setConnectionDataProp('globals', 'url', _applicationUrl, true);
    _utilsTableau.setAuthData('clientId', clientId);
    _utilsTableau.setAuthData('clientSecret', clientSecret);
    if (tableau.phase === tableau.phaseEnum.authPhase) {
      _getAccessToken(tableau.submit);
    } else {
      _getAccessToken(_getEntities);
    }
  }

  function _next() {
    _biz_uiTableau.disableBtn('nextButton');
    var selectedEntities = _biz_globalsTableau.selectedEntities,
    entityIndexes = Object.keys(selectedEntities),
    showNextStep = false;

    for (var i = 0; i < entityIndexes.length; i++) {
      var entity = _biz_globalsTableau.entities[entityIndexes[i]],
      attribs = Object.keys(selectedEntities[entityIndexes[i]]),
      queryByProcess = false;
      for (var j = 0; j < attribs.length; j++) {
        if (attribs[j].indexOf("@") !== -1) {
          queryByProcess = true;
          showNextStep = true;
        }
      }
      if (queryByProcess && !_biz_globalsTableau.processByEntity[entityIndexes[i]]) {
        _biz_globalsTableau.processByEntity[entityIndexes[i]] = entity.processesRelated[0].id;
      } else if (!queryByProcess) {
        delete _biz_globalsTableau.processByEntity[entityIndexes[i]];
      }
    }

    var processByEntityKeys = Object.keys(_biz_globalsTableau.processByEntity);
    for (var i = 0; i < processByEntityKeys.length; i++) {
      var entity = _biz_globalsTableau.selectedEntities[processByEntityKeys[i]];
      if (!entity) {
        delete _biz_globalsTableau.processByEntity[processByEntityKeys[i]];
      }
    }

    var backToTree = _biz_uiTableau.renderTmpl.bind(this, "bizagi.connector.tableau.tree", _biz_uiTableau.loadTreeView.bind(this, _next, true));
    var resumeStep = _biz_uiTableau.renderTmpl.bind(
    this,
    "bizagi.connector.tableau.export",
    _biz_uiTableau.loadResumeConfiguration.bind(this, _submit, _next));

    if (showNextStep) {
      _biz_uiTableau.renderTmpl("bizagi.connector.tableau.processTable", _biz_uiTableau.loadEntityProcessTable.bind(this, resumeStep, backToTree));
    } else {
      resumeStep();
    }
  }

  function _submit() {
    var entityIndexes = Object.keys(_biz_globalsTableau.selectedEntities);
    for (var i = 0; i < entityIndexes.length; i++) {
      var entity = _biz_globalsTableau.entities[entityIndexes[i]],
      attribs = Object.keys(_biz_globalsTableau.selectedEntities[entityIndexes[i]]),
      validAttribs = [],processId = null,
      queryByProcess = false;
      validAttribs.push("BizagiId");
      for (var j = 0; j < attribs.length; j++) {
        validAttribs.push(attribs[j].substring(entity.name.length + 1, attribs[j].length));
        if (attribs[j].indexOf("@") !== -1) {
          queryByProcess = true;
          showNextStep = true;
        }
      }
      if (queryByProcess)
      processId = _biz_globalsTableau.processByEntity[entityIndexes[i]];

      _utilsTableau.setConnectionDataProp('entities', entity.name, {
        "entityIndex": entityIndexes[i],
        "queryByProcess": queryByProcess,
        "processId": processId,
        "attribs": validAttribs });

    }
    tableau.connectionData = JSON.stringify(_biz_globalsTableau.connectionData);
    tableau.submit();
  }

  function _getSchema(schemaCallback) {
    var dataConnector = _utilsTableau.getConnectionData(),
    entitiesMap = dataConnector.entities,
    entitiesList = Object.keys(entitiesMap),
    tables = [],tableId = '';

    for (var i = 0; i < entitiesList.length; i++) {
      tableId = _parseString(entitiesList[i]);
      tables.push({
        id: tableId,
        alias: tableId,
        columns: _generateColumns(entitiesMap[entitiesList[i]].attribs) });

    }

    schemaCallback(tables);
  }

  function _generateColumns(attributes) {
    var tableCols = [];
    for (var i = 0; i < attributes.length; i++) {
      tableCols.push({
        id: _parseString(attributes[i]),
        dataType: tableau.dataTypeEnum.string });

    }
    return tableCols;
  }

  function _stepCompleteCallback(table, doneCallback, numPages) {
    var currentPages = 0;
    return function () {
      currentPages++;
      if (numPages === 0 || numPages === currentPages) {
        table.appendRows(_biz_globalsTableau.dataRows);
        return doneCallback();
      }
    };
  }

  function _getDataCallback(table, doneCallback, retries) {
    if (!retries) retries = 0;
    var dataConnector = _utilsTableau.getConnectionData(),
    entityName = table.tableInfo.id,
    entity = dataConnector.entities[entityName],
    attributes = entity.attribs,
    baseUrl = dataConnector.globals.url + "/odata/query/executeEntityQuery",
    bodyRequest = {
      "queryParameter": {
        "entity": entityName, "xpaths": [] } };



    if (entity.queryByProcess) {
      bodyRequest.queryParameter["processId"] = entity.processId;
    }
    for (var i = 0; i < attributes.length; i++) {
      if (attributes[i] === "BizagiId") continue;
      bodyRequest.queryParameter.xpaths.push({
        "xpath": attributes[i] });

    }
    _biz_globalsTableau.dataRows = [];

    var mainDeferred = $.Deferred();
    _getData({
      url: baseUrl + "?$top=" + _utilsTableau.ITEMS_PAGE,
      table: table,
      bodyRequest: bodyRequest,
      deferred: mainDeferred });


    $.when(mainDeferred).done(function (totalRecords) {
      var numPages = _utilsTableau.calculateNumOfPages(totalRecords) - 1,
      doneFn = _stepCompleteCallback(table, doneCallback, numPages);
      for (var i = 1; i <= numPages; i++) {
        var page = "?$skip=" + i * _utilsTableau.ITEMS_PAGE + "&$top=" + _utilsTableau.ITEMS_PAGE;
        _getDataWrapper({
          url: baseUrl + page,
          table: table,
          bodyRequest: bodyRequest,
          doneCallback: doneFn });

      }
      if (numPages === 0)
      doneFn();else
      if (numPages === -1)
      return doneCallback();
    }).fail(function (err) {
      _retry(err, retries + 1, _getDataCallback.bind(this, table, doneCallback, retries + 1));
    });
  }

  function _getDataWrapper(requestObj, retries) {
    if (!retries) retries = 0;

    var deferred = $.Deferred();
    requestObj.deferred = deferred;

    _getData(requestObj);

    $.when(deferred).done(function () {
      requestObj.doneCallback();
    }).fail(function (err) {
      _retry(err, retries + 1, _getDataWrapper.bind(this, requestObj, retries + 1));
    });
  }

  function _getData(requestObj) {
    var dataConnector = _utilsTableau.getConnectionData(),
    authData = _utilsTableau.getAuthData(),
    entityName = requestObj.table.tableInfo.id,
    attributes = dataConnector.entities[entityName].attribs;

    $.ajax({
      type: "POST",
      url: requestObj.url,
      dataType: 'json',
      data: requestObj.bodyRequest,
      beforeSend: function beforeSend(request) {
        request.setRequestHeader('Authorization', 'Bearer ' + authData.accessToken);
      },

      success: function success(data) {
        var result = data.value;
        for (var i = 0; i < result.length; i++) {
          var record = {};
          for (var j = 0; j < attributes.length; j++) {
            var colId = _parseString(attributes[j]),
            attributeName = attributes[j];
            if (attributeName[0] === '@') {
              var firstLetterLowerCase = attributeName[1].toLowerCase();
              attributeName = attributeName.substring(2, attributeName.length);
              attributeName = firstLetterLowerCase + attributeName;
            }
            record[colId] = result[i][attributeName];
          }
          _biz_globalsTableau.dataRows.push(record);
        }
        if (result.length > 0) {
          tableau.reportProgress("Bizagi status: " + _biz_globalsTableau.dataRows.length + ' of ' + data["@odata.totalCount"] + ' records');
          requestObj.deferred.resolve(data["@odata.totalCount"]);
        } else {
          requestObj.deferred.resolve(0);
        }
      },

      error: function error(err) {requestObj.deferred.reject(err);} });

  }

  function _getAccessToken(callback) {
    var dataConnector = tableau.phase === tableau.phaseEnum.interactivePhase ? _biz_globalsTableau.connectionData : _utilsTableau.getConnectionData(),
    authData = _utilsTableau.getAuthData();
    $.ajax({
      type: "POST",
      url: dataConnector.globals.url + '/oauth2/server/token',
      contentType: 'application/x-www-form-urlencoded',
      data: 'grant_type=client_credentials&scope=api',
      dataType: 'json',
      beforeSend: function beforeSend(request) {
        request.setRequestHeader('Authorization', 'Basic ' + btoa(authData.clientId + ':' + authData.clientSecret));
      },

      success: function success(data) {
        if (data.access_token) {
          _utilsTableau.setAuthData('accessToken', data.access_token);
          if (callback !== undefined && typeof callback === 'function') callback();
        } else {
          _biz_uiTableau.showFailedConnectionMessage();
          tableau.log("Bizagi Error: " + JSON.stringify(data));
          tableau.abortForAuth("Bizagi Error: Wrong credentials");
        }
      },

      error: function error(err) {
        _biz_uiTableau.showFailedConnectionMessage();
        tableau.log("Bizagi Error: " + JSON.stringify(err));
        if (err.status === 401)
        tableau.abortForAuth("Bizagi Error: Wrong credentials");else

        tableau.abortWithError("Bizagi Error: See log for details");
      } });

  }

  function _getEntities(retries) {
    if (!retries) retries = 0;
    var dataConnector = _biz_globalsTableau.connectionData,
    authData = _utilsTableau.getAuthData();
    $.ajax({
      type: "GET",
      url: dataConnector.globals.url + '/odata/query/entity?$orderby=name&$filter=type eq \'Master\'',
      dataType: 'json',
      beforeSend: function beforeSend(request) {
        request.setRequestHeader('Authorization', 'Bearer ' + authData.accessToken);
      },

      success: function success(response) {
        var entities = response.value;
        if (entities.length > 0) {
          _biz_globalsTableau.entities = entities;
        }
        _biz_uiTableau.renderTmpl("bizagi.connector.tableau.tree", _biz_uiTableau.loadTreeView.bind(this, _next));
      },

      error: function error(err) {_retry(err, retries + 1, _getEntities.bind(this, retries + 1));} });

  }

  function _retry(err, retries, callback) {
    console.log(retries);
    if (err.status === 401 && retries < 3) {
      _getAccessToken(callback);
    } else {
      tableau.log("Bizagi Retry Error: " + JSON.stringify(err));
      tableau.log("Number of retries: " + retries);
      var message = err.responseJSON ? err.responseJSON.message : "See log for details";
      tableau.abortWithError("Bizagi Error: " + message);
    }
  }

  function _parseString(str) {
    var newStr = str.replace(/ /g, '_').
    replace(/"/g, '_doublequote_').
    replace(/,/g, '_comma_').
    replace(/=/g, '_equal_').
    replace(/\//g, '_fslash_').
    replace(/-/g, '_dash_').
    replace(/\./g, '_dot_').
    replace(/[^A-Za-z0-9_]/g, '_');
    return newStr;
  }

  return {
    buildConnector: buildConnector,
    connect: connect,
    getSchema: _getSchema,
    getData: _getDataCallback,
    setApplicationUrl: setApplicationUrl,
    getApplicationUrl: getApplicationUrl };

};

var UtilsTableau = function UtilsTableau() {
  var ITEMS_PAGE = 50;

  function setAuthData(prop, value) {
    var authData = {
      clientId: "",
      clientSecret: "",
      accessToken: "" };

    if (tableau.password) {
      authData = getAuthData();
    }
    authData[prop] = value;

    tableau.password = JSON.stringify(authData);
  }

  function setConnectionDataProp(section, prop, value, restart) {
    if (!restart) restart = false;
    if (restart) {
      _biz_globalsTableau.connectionData = {
        globals: {},
        entities: {} };

    }
    if (!prop && !value) {
      _biz_globalsTableau.connectionData[section] = {};
    } else if (!prop) {
      _biz_globalsTableau.connectionData[section] = value;
    } else {
      _biz_globalsTableau.connectionData[section][prop] = value;
    }
  }

  function getConnectionData() {
    try {
      return JSON.parse(tableau.connectionData);
    } catch (ex) {
      tableau.log("[Exception] Error parsing connection data: " + ex);
      tableau.log("[Data] Error parsing connection data: " + tableau.connectionData);
      tableau.abortWithError("Error: problem parsing connection data. See log for details.");
      return {
        globals: {},
        entities: {} };

    }
  }

  function getAuthData() {
    try {
      return JSON.parse(tableau.password);
    } catch (ex) {
      tableau.log("[Exception] Error parsing auth data: " + ex);
      tableau.log("[Data] Error parsing auth data: " + tableau.password);
      tableau.abortWithError("Error: problem parsing authentication data. See log for details.");
      return {
        clientId: "",
        clientSecret: "",
        accessToken: "" };

    }
  }

  function calculateNumOfPages(totalRecords) {
    var pages = Math.floor(totalRecords / ITEMS_PAGE);
    if (totalRecords % ITEMS_PAGE > 0)
    pages++;
    return pages;
  }

  /**
     * Checks if tableau version is greater than or less than 2019.3 to know if webengine is in last version
     */
  function isNewWebEngine() {
    if (tableau.platformVersion) {
      var versionSplitted = tableau.platformVersion.split(".");
      return parseInt(versionSplitted[0]) === 2019 && parseInt(versionSplitted[1]) >= 4 || parseInt(versionSplitted[0]) > 2019;
    }
    return false;
  }

  return {
    setAuthData: setAuthData,
    setConnectionDataProp: setConnectionDataProp,
    calculateNumOfPages: calculateNumOfPages,
    getConnectionData: getConnectionData,
    getAuthData: getAuthData,
    isNewWebEngine: isNewWebEngine,
    ITEMS_PAGE: ITEMS_PAGE };

};

var GlobalsTableau = function GlobalsTableau() {
  var entities = [],
  selectedEntities = {},
  processByEntity = {},
  dataRows = [],
  connectionData = {
    globals: {},
    entities: {} };


  return {
    entities: entities,
    selectedEntities: selectedEntities,
    dataRows: dataRows,
    processByEntity: processByEntity,
    connectionData: connectionData };

};

(function () {
  _biz_uiTableau = new UITableau(),
  _biz_globalsTableau = new GlobalsTableau();
  _biz_bizagiTableau = new BizagiTableau();

  _deferredInitUi = $.Deferred();
  _deferredInitWDC = $.Deferred();

  var bizagiConnector = tableau.makeConnector();
  bizagiConnector.init = function (initCallback) {
    tableau.authType = tableau.authTypeEnum.custom;
    initCallback();
    _deferredInitWDC.resolve();
  };
  bizagiConnector.getSchema = _biz_bizagiTableau.getSchema;
  bizagiConnector.getData = _biz_bizagiTableau.getData;

  tableau.registerConnector(bizagiConnector);

  $.when(_deferredInitUi, _deferredInitWDC).done(function () {
    if ((tableau.phase === tableau.phaseEnum.authPhase || tableau.phase === tableau.phaseEnum.interactivePhase) &&
    tableau.connectionData) {
      var utilsTableau = new UtilsTableau(),
      dataConnector = utilsTableau.getConnectionData();
      if (dataConnector.globals.url === _biz_bizagiTableau.getApplicationUrl() && tableau.password) {
        var authData = utilsTableau.getAuthData();
        $("#clientId").val(authData.clientId);
        $("#clientSecret").val(authData.clientSecret);
        _biz_bizagiTableau.connect($("#clientId").val(), $("#clientSecret").val());
      }
    }
  });
})();